:root {
  --color-text: #333;
  --color-bg: #fff;
  --color-surface: #f9f9f9;
  --color-primary: #333;
  --color-primary-hover: #000;
  --color-border: #0000001a;
  --color-success: #4caf50;
  --color-warning: #ff9800;
  --color-danger: #f44336;
  --radius-sm: 4px;
  --radius-md: 8px;
  --radius-lg: 12px;
  --shadow-sm: 0 2px 6px #0000000d;
  --color-player-card: #2e5a88;
  --color-computer-card: #a64452;
  --card-gradient-overlay: linear-gradient(145deg, #ffffff1f 0%, #fff0 50%, #00000014 100%);
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: inherit;
}

body {
  background-color: var(--color-bg);
  color: var(--color-text);
  overscroll-behavior: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  min-height: 100vh;
  margin: 0;
  font-family: sans-serif;
  line-height: 1.5;
  display: flex;
  overflow-x: hidden;
}

header, main, #result-display {
  flex-direction: column;
  align-items: center;
  width: 100%;
  display: flex;
}

main {
  width: 100%;
  max-width: 800px;
  padding: .5rem;
}

#game-container {
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  width: 100%;
  display: flex;
}

@media (width >= 768px) {
  main {
    padding: 1rem;
  }

  #game-container {
    gap: 2rem;
  }
}

#game-stats {
  flex-wrap: wrap;
  justify-content: center;
  gap: .5rem;
  width: 100%;
  display: flex;
}

#game-progress-container {
  text-align: center;
  flex: 0 0 100%;
  order: -1;
  align-self: center;
  padding: .25rem;
}

.stats {
  flex-direction: column;
  flex: 40%;
  gap: .25rem;
  min-width: 0;
  padding: .25rem 0;
  display: flex;
}

.score-row {
  font-size: .9rem;
  font-weight: bold;
}

.bar-wrapper {
  border: 2px solid var(--color-text);
  border-radius: var(--radius-sm);
  background-color: var(--color-danger);
  width: 100%;
  max-width: 300px;
  height: 30px;
  position: relative;
  overflow: hidden;
  box-shadow: inset 0 2px 4px #00000040, inset 0 -4px 6px #0a0a0a33;
}

.bar {
  background-color: var(--color-success);
  width: 100%;
  height: 100%;
  transition: width .4s ease-out;
  position: absolute;
  top: 0;
  left: 0;
}

.bar-text {
  color: #fff;
  z-index: 5;
  text-shadow: 1px 1px 2px #000;
  white-space: nowrap;
  pointer-events: none;
  font-size: .9rem;
  font-weight: bold;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

#player-stats {
  text-align: left;
  align-items: flex-start;
}

#player-stats .bar-text {
  left: 8px;
}

#computer-stats {
  text-align: right;
  align-items: flex-end;
}

#computer-stats .bar {
  left: auto;
  right: 0;
}

#computer-stats .bar-text {
  right: 8px;
}

@media (width >= 768px) {
  #game-stats {
    grid-template-columns: 1fr auto 1fr;
    align-items: end;
    gap: 0;
    display: grid;
  }

  #game-progress-container {
    order: 0;
    min-width: 180px;
    padding: 0 2rem;
  }

  .stats {
    gap: .5rem;
    width: auto;
  }

  .bar-wrapper {
    height: 40px;
  }

  .bar-text {
    font-size: 1.1rem;
  }

  .score-row {
    font-size: 1rem;
  }
}

#choices, #move-reveal {
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  gap: .5rem;
  width: 100%;
  display: flex;
}

.card, .card-button {
  aspect-ratio: 2 / 3;
  perspective: 1200px;
  touch-action: manipulation;
  background: none;
  border: none;
  flex: 1 1 0;
  min-width: 80px;
  max-width: 130px;
  padding: 0;
  transition: transform .6s cubic-bezier(.175, .885, .32, 1.275), opacity .4s ease-out;
  position: relative;
}

.card-inner {
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: transform 1s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.card-inner.is-flipped {
  transform: rotateY(180deg);
}

.card-front, .card-back {
  backface-visibility: hidden;
  border-radius: var(--radius-lg);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  inset: 0;
}

.card-back {
  backface-visibility: hidden;
  border: 1px solid #00000026;
  transform: rotateY(0)translateZ(1px);
  box-shadow: 0 4px 8px #0000001a;
}

.card-front {
  background-color: var(--color-surface);
  backface-visibility: hidden;
  border: 1px solid #00000026;
  transform: rotateY(180deg)translateZ(1px);
  box-shadow: inset 0 0 15px #00000005;
}

.card-back.player-theme {
  background-color: var(--color-player-card);
  background-image: var(--card-gradient-overlay), repeating-linear-gradient(45deg, #ffffff08 0px, #ffffff08 1px, transparent 1px, transparent 4px);
}

.card-back.computer-theme {
  background-color: var(--color-computer-card);
  background-image: var(--card-gradient-overlay), repeating-linear-gradient(-45deg, #ffffff08 0px, #ffffff08 1px, transparent 1px, transparent 4px);
}

.card-button {
  cursor: pointer;
}

.interaction-locked .card-button {
  pointer-events: none;
  cursor: default;
}

@media (hover: hover) {
  .card-button:not(:disabled):hover {
    transform: translateY(-5px);
  }

  .card-button:not(:disabled):hover .card-front {
    background-color: #fff;
    border-color: #0000004d;
  }
}

.card-button:disabled {
  opacity: 1;
  filter: none;
  cursor: not-allowed;
}

.card-button:disabled .card-front {
  filter: grayscale(.9) contrast(.9);
  opacity: .7;
  cursor: not-allowed;
  background-color: #eee;
}

.card-button:disabled .icon, .card-button:disabled .label {
  opacity: .5;
}

.icon {
  filter: drop-shadow(0 4px 4px #0000001a);
  font-size: 2.25rem;
  line-height: 1;
}

.label {
  color: #666;
  text-transform: uppercase;
  letter-spacing: .5px;
  font-size: .65rem;
  font-weight: 700;
}

.vs-label {
  color: #ddd;
  margin: 0 .25rem;
  font-size: 1.25rem;
  font-style: italic;
  font-weight: 900;
}

@media (width >= 768px) {
  #choices, #move-reveal {
    gap: 1.5rem;
  }

  .card, .card-button {
    flex: 0 120px;
  }

  .icon {
    font-size: 4rem;
  }

  .label {
    font-size: .9rem;
  }

  .vs-label {
    margin: 0 .5rem;
    font-size: 1.5rem;
  }
}

.winner-highlight {
  z-index: 100;
  border-radius: 12px;
  position: relative;
  overflow: hidden;
  transform: scale(1.05)rotate(-2deg);
  box-shadow: 0 0 0 3px #b8860b, 0 0 20px #b8860b99, inset 0 0 20px #ffd7004d;
  border: none !important;
  transition: none !important;
  animation: 2s ease-in-out infinite pulse-prestige !important;
}

@media (width >= 768px) {
  .winner-highlight {
    transform: scale(1.15)rotate(-2deg);
  }
}

@keyframes pulse-prestige {
  0%, 100% {
    box-shadow: 0 0 0 3px #b8860b, 0 0 15px #b8860b99;
  }

  50% {
    box-shadow: 0 0 0 5px gold, 0 0 30px #ffd700cc, inset 0 0 10px #ffffff80;
  }
}

.winner-highlight:after {
  content: "";
  background: linear-gradient(45deg, #0000 45%, #ffffff4d 50%, #0000 55%);
  width: 200%;
  height: 200%;
  animation: 3s infinite sweep;
  position: absolute;
  top: -50%;
  left: -50%;
}

@keyframes sweep {
  0% {
    transform: translateX(-100%)translateY(-100%);
  }

  100% {
    transform: translateX(100%)translateY(100%);
  }
}

#move-reveal {
  perspective: 1200px;
  overflow: visible;
}

.card.entering-player {
  opacity: 0;
  transform: translateX(-150%)rotate(-10deg);
}

.card.entering-computer {
  opacity: 0;
  transform: translateX(150%)rotate(10deg);
}

.card.slide-in {
  opacity: 1;
  transform: translateX(0)rotate(0);
}

.card-defeated {
  opacity: .6;
  pointer-events: none;
  z-index: 0;
  filter: grayscale() brightness(.3) sepia(.1) !important;
  transform: translateY(40px) rotate(calc(var(--facing, 1) * -20deg)) scale(.85) !important;
  box-shadow: none !important;
  transition: all .8s cubic-bezier(.175, .885, .32, 1.275) !important;
}

.card-defeated:before {
  content: "";
  z-index: 10;
  pointer-events: none;
  background-image: url("data:image/svg+xml;utf8,<svg xmlns=\"http://www.w3.org/2000/svg\" width=\"100\" height=\"100\" viewBox=\"0 0 100 100\"><path d=\"M0 20 L30 40 L10 70 M50 0 L40 50 L80 100 M100 20 L70 30\" stroke=\"rgba(255,255,255,0.2)\" fill=\"none\"/></svg>");
  background-size: cover;
  position: absolute;
  inset: 0;
}

.hidden {
  display: none !important;
}

.visually-hidden {
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

#main-menu {
  flex-direction: column;
  flex: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

.menu-content {
  text-align: center;
  flex-direction: column;
  gap: 1.5rem;
  width: 100%;
  max-width: 90%;
  padding: 1rem;
  display: flex;
}

@media (width >= 768px) {
  .menu-content {
    gap: 2.5rem;
    max-width: 400px;
    padding: 2rem;
  }
}

.title-large {
  color: var(--color-primary);
  text-transform: uppercase;
  letter-spacing: -1px;
  font-size: 2rem;
  font-weight: 800;
  line-height: 1.1;
}

@media (width >= 768px) {
  .title-large {
    font-size: 3rem;
  }
}

.menu-controls {
  flex-direction: column;
  gap: .75rem;
  width: 100%;
  display: flex;
}

button {
  touch-action: manipulation;
  font-family: inherit;
  font-size: 1rem;
}

button:focus {
  outline: none;
}

button:focus-visible {
  outline: 3px solid var(--color-warning);
  outline-offset: 2px;
  border-radius: var(--radius-md);
}

.btn-primary, .btn-secondary, #play-again {
  border-radius: var(--radius-md);
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: .5px;
  padding: .75rem 1.25rem;
  font-weight: 700;
  transition: all .2s;
}

.btn-primary, #play-again {
  background-color: var(--color-primary);
  color: #fff;
  border: 2px solid var(--color-primary);
}

.btn-primary:hover, #play-again:hover {
  background-color: var(--color-primary-hover);
  border-color: var(--color-primary-hover);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px #00000026;
}

.btn-secondary {
  color: var(--color-text);
  border: 2px solid var(--color-border);
  background-color: #0000;
}

.btn-secondary:hover {
  border-color: var(--color-primary);
  background-color: #fff;
}

.btn-primary:active, .card-button:active {
  filter: brightness(.9);
  transform: scale(.98);
}

#arena {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: .5rem;
  width: 100%;
  min-height: 180px;
  display: flex;
}

@media (width >= 768px) {
  #arena {
    gap: 1rem;
    min-height: 250px;
  }
}

#announcement-container {
  text-align: center;
  min-height: 2rem;
}

#announcement-container h2 {
  margin: 0;
  font-size: 1.25rem;
}

@media (width >= 768px) {
  #announcement-container {
    min-height: 2.5rem;
  }

  #announcement-container h2 {
    font-size: 1.5rem;
  }
}

#status-container {
  text-align: center;
  flex-direction: column;
  align-items: center;
  width: 100%;
  min-height: 2.5rem;
  display: flex;
}

#game-controls {
  flex-direction: column;
  align-items: center;
  width: 100%;
  display: flex;
}

#choices, #progression-zone {
  justify-content: center;
  width: 100%;
  display: flex;
}

.stance-rock {
  animation: .5s cubic-bezier(.36, 0, .66, -.56) forwards rock-slam;
}

@keyframes rock-slam {
  0% {
    transform: translateY(0);
  }

  30% {
    transform: translateY(-40px) rotate(calc(-5deg * var(--facing, 1)));
  }

  100% {
    transform: translateY(0);
  }
}

.stance-paper {
  animation: 1.2s ease-in-out infinite alternate paper-float;
}

@keyframes paper-float {
  0% {
    transform: scale(1)translateY(0);
  }

  100% {
    transform: scale(1.1) translateY(-15px) rotate(calc(2deg * var(--facing, 1)));
  }
}

.stance-scissors {
  animation: .3s ease-in-out 2 scissors-strike;
}

@keyframes scissors-strike {
  0% {
    transform: rotate(0);
  }

  50% {
    transform: rotate(calc(15deg * var(--facing, 1))) scale(1.05);
  }

  100% {
    transform: rotate(0);
  }
}

.stance-tara {
  animation: 1.5s ease-in-out infinite alternate tara-ascend;
}

@keyframes tara-ascend {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(-15px);
  }
}

.arena-shake {
  animation: .2s ease-in-out shake;
}

@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }

  25% {
    transform: translateX(-5px);
  }

  75% {
    transform: translateX(5px);
  }
}

.card-impact {
  z-index: 50;
  animation: .4s cubic-bezier(.36, .07, .19, .97) both impact-shake;
}

@keyframes impact-shake {
  10%, 90% {
    transform: translate3d(-2px, 0, 0)scale(.95);
  }

  20%, 80% {
    transform: translate3d(4px, 0, 0)scale(.9);
  }

  30%, 50%, 70% {
    filter: brightness(1.5) contrast(1.2);
    transform: translate3d(-6px, 0, 0);
  }

  40%, 60% {
    transform: translate3d(6px, 0, 0);
  }
}
